/* Google Font */
@import url('https://fonts.googleapis.com/css2?family=Manrope&display=swap');  
@import url('https://fonts.googleapis.com/css2?family=Carattere&display=swap');

/* Body Styles  */
body {
    font-family: 'Manrope', sans-serif;
    min-height: 105vh;
}

.loading {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
}

/* Navbar Styles  */
nav {
    margin-bottom: 20px;
    border-bottom: 2px groove;
}

.navbar-brand {
    font-size: 1.8rem;
    font-weight: bold;
    text-align: center;
    margin-left: 20px;
}

.navbar-nav a {
    font-size: 1.3rem;
}

.navbar-nav a:hover {
   transform: scale(1.3);
   transition: ease-out 0.2s;
   color: blueviolet;
   text-decoration: underline;
}

.navbar-nav a:active {
   transform: scale(0.6);
   transition: ease-out 0.2s;
}

.mode i {
    font-size: 1.5rem;
    padding-top:10px;
}

.mode i:hover {
   color: rgb(137, 43, 226);
}

lord-icon {
    width: 40px;
    height: 40px;
}

/* Head Style */
.head {
    margin-bottom: 20px;
}

.head h1{
   font-family: 'Carattere', cursive;
   text-align: center;
   font-size: 4rem;
}

.head hr {
    max-width: 80%;
    margin: auto;
}

/* Home Style */
.main-left {
    padding: 10px;
    border: 3px solid blueviolet;
    border-radius: 10px;
    margin: 10px;
}

.main-left p {
    font-size: 1.2rem;
}
.main-right {
    padding: 10px;
    border: 3px solid orangered;
    border-radius: 10px;
    margin: 10px;
}

/* Resume Style */
.exp {
    margin-bottom: 20px;
}



.exp .title a {
    text-decoration: none;
    font-size: 2rem;
}

.exp .time {
    font-size: 1rem;
    font-weight: bold;
    background-color: #8bcef8;
    padding: 10px;
    margin: 10px;
    border-radius: 10px;
}

.tasklist {
    list-style-position: inside;
    padding: 3px;
    border-left: solid 2px orangered;
    margin-top: 10px;
}

.task {
    font-size: 1.3rem;
}

.edu {
    border-radius: 10px;
    margin-bottom: 20px;
}



.edu .title {
    font-size: 2rem;
    margin-bottom: 15px;
}

.edu .time {
    font-size: 1rem;
    font-weight: bold;
    background-color: #8bcef8;
    padding: 10px;
    border-radius: 10px;
}

.edu .college {
    font-size: 1.6rem;
    font-family: monospace;
    list-style-position: inside;
    padding: 5px;
    border-left: solid 2px #9900FF;
    margin-top: 10px;
}

.edu .major {
    display:inline-block;
    font-size: 1.2rem;
}

/* Skill */
.skills {
    padding: 10px;
    border-radius: 10px;
    backdrop-filter: blur(18px) saturate(173%);
    -webkit-backdrop-filter: blur(18px) saturate(173%);
    background-color: rgba(17, 25, 40, 0.027);
    border-radius: 12px;
    border: 1px solid rgba(255, 255, 255, 0.021);
    margin-bottom: 10px;
}

.skill {
    font-size: 1.3rem;
    font-size: 1.4rem;
}

.progress-bar {
    background-color: #5151FB;
}

/* Socials */
.socials {
    display: flex;
    justify-content: space-around;
    backdrop-filter: blur(18px) saturate(173%);
    background-color: rgba(17, 25, 40, 0.027);
    border-radius: 20px;
    padding: 20px;
}

.socials i {
    font-size: 5rem;
    color: blueviolet;
}


/* media query */
@media only screen and (max-width: 450px) {

    .navbar-brand {
        font-size: 1.5rem;
    }
}

@media only screen and (max-width:700px) {
    .edu {
        border: none;
    }

    .edu .title {
        font-size: 1.6rem;
    }

    .task {
        font-size: 1.2rem;
    }

    .exp {
        border: none;
    }

    .exp .title a {
        font-size: 1.6rem;
    }

    .socials i{
        font-size: 3rem;
        justify-content: space-between;
    }
    
    .socials {
        padding: 10px;
    }
}